<template>
  <DxDataGrid v-if="data_type == 'trials'" id="trials_table" :ref="trialsTableRef" :height="510" :data-source="trials"
    key-expr="Trial_ID" :show-borders="true" :selection="{ mode: 'single' }" :hover-state-enabled="true"
    :allow-column-resizing="true">
    <DxPaging :page-size="page_size" />
    <DxPager :visible="true" :allowed-page-sizes="[5, 10, 'all']" :display-mode="'full'" :show-page-size-selector="true"
      :show-info="true" :show-navigation-buttons="true" />
    <DxFilterRow :visible="true" :apply-filter="'auto'" />
    <DxHeaderFilter :visible="true" />
    <DxSearchPanel :visible="true" :width="240" placeholder="Search..." />
    <DxColumnChooser :enabled="true" />
    <DxColumnFixing :enabled="true" />
    <DxColumn type="buttons" :width="110">
      <DxButton hint="Copy" icon="copy" @click="copyIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Details" icon="link" @click="detailsIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Related Entry" icon="share" @click="relationIconClick" />
    </DxColumn>
    <DxColumn :width="60" :fixed="true" data-field="Trial_ID" caption="ID" cell-template="id-cell-template">
    </DxColumn>
    <template #id-cell-template="{ data }">
      {{ data.data["Trial_ID"] }}
    </template>
    <DxColumn :width="110" :fixed="true" data-field="Source_ID" caption="Source ID"
      cell-template="nct_id-cell-template">
    </DxColumn>
    <template #nct_id-cell-template="{ data }">
      <a :href="'https://clinicaltrials.gov/show/' + data.data['Source_ID']" target="_blank">{{
          data.data["Source_ID"]
      }}</a>
    </template>
    <DxColumn :width="200" :fixed="true" data-field="Title" caption="Title">
    </DxColumn>
    <DxColumn :width="90" data-field="Status" caption="Status">
    </DxColumn>
    <DxColumn :width="100" data-field="Study Results" caption="Results" cell-template="results-cell-template">
    </DxColumn>
    <template #results-cell-template="{ data }">
      <a :href="data.data['Results URL']" target="_blank">{{
          data.data["Study Results"]
      }}</a>
    </template>
    <DxColumn :width="170" data-field="Conditions" caption="Conditions">
    </DxColumn>
    <DxColumn :width="80" data-field="Gender" caption="Gender">
    </DxColumn>
    <DxColumn :width="150" data-field="Age" caption="Age">
    </DxColumn>
    <DxColumn :width="90" data-field="Enrollment" caption="Enrollment">
    </DxColumn>
    <DxColumn :width="90" data-field="Phases" caption="Phases">
    </DxColumn>
    <DxColumn :width="120" data-field="Start Date" caption="Start Date">
    </DxColumn>
    <DxColumn :width="120" data-field="Completion Date" caption="Completion Date">
    </DxColumn>
    <DxMasterDetail :enabled="true" template="masterDetailTemplate" />
    <template #masterDetailTemplate="{ data: trial_detail }">
      <TrialDetailTemplate :template-data="trial_detail" />
    </template>
  </DxDataGrid>

  <DxDataGrid v-if="data_type == 'drugs'" id="drugs_table" :ref="drugsTableRef" :height="510" :data-source="drugs"
    key-expr="Drug_ID" :show-borders="true" :selection="{ mode: 'single' }" :hover-state-enabled="true"
    :allow-column-resizing="true">
    <DxPaging :page-size="page_size" />
    <DxPager :visible="true" :allowed-page-sizes="[5, 10, 'all']" :display-mode="'full'" :show-page-size-selector="true"
      :show-info="true" :show-navigation-buttons="true" />
    <DxFilterRow :visible="true" :apply-filter="'auto'" />
    <DxHeaderFilter :visible="true" />
    <DxSearchPanel :visible="true" :width="240" placeholder="Search..." />
    <DxColumnChooser :enabled="true" />
    <DxColumnFixing :enabled="true" />
    <DxColumn type="buttons" :width="110">
      <DxButton hint="Copy" icon="copy" @click="copyIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Details" icon="link" @click="detailsIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Related Entry" icon="share" @click="relationIconClick" />
    </DxColumn>
    <DxColumn :width="60" :fixed="true" data-field="Drug_ID" caption="ID" cell-template="id-cell-template">
    </DxColumn>
    <template #id-cell-template="{ data }">
      {{ data.data["Drug_ID"] }}
    </template>
    <DxColumn :width="140" :fixed="true" data-field="Drug_Name" caption="Drug Name">
    </DxColumn>
    <DxColumn :width="140" data-field="Drug_Type" caption="Drug Type">
    </DxColumn>
    <DxColumn :width="140" data-field="DrugBank_ID" caption="DrugBank ID" cell-template="drugbank_id-cell-template">
    </DxColumn>
    <template #drugbank_id-cell-template="{ data }">
      <a :href="'https://go.drugbank.com/drugs/' + data.data['DrugBank_ID']" target="_blank">{{
          data.data["DrugBank_ID"]
      }}</a>
    </template>
    <DxColumn :width="140" data-field="PubChem_ID" caption="PubChem ID" cell-template="pubchem_id-cell-template">
    </DxColumn>
    <template #pubchem_id-cell-template="{ data }">
      <a :href="'https://pubchem.ncbi.nlm.nih.gov/compound/' + data.data['PubChem_ID']" target="_blank">{{
          data.data["PubChem_ID"]
      }}</a>
    </template>
    <DxColumn :width="140" data-field="CasNo" caption="CAS RN">
    </DxColumn>
    <DxColumn :width="210" data-field="Repositioning for NAFLD" caption="Repositioning for NAFLD">
    </DxColumn>
    <DxColumn :width="180" data-field="Therapeutic_Category" caption="Therapeutic Category">
    </DxColumn>
    <DxColumn :width="200" data-field="Clinical_Trial_Progress" caption="Clinical Trial Progress">
    </DxColumn>
    <DxColumn :width="180" data-field="Latest_Progress" caption="Latest Progress">
    </DxColumn>
    <DxMasterDetail :enabled="true" template="masterDetailTemplate" />
    <template #masterDetailTemplate="{ data: drug_detail }">
      <DrugDetailTemplate :template-data="drug_detail" />
    </template>
  </DxDataGrid>

  <DxDataGrid v-if="data_type == 'targets'" id="targets_table" :ref="targetsTableRef" :height="510"
    :data-source="targets" key-expr="Target_ID" :show-borders="true" :selection="{ mode: 'single' }"
    :hover-state-enabled="true" :allow-column-resizing="true">
    <DxPaging :page-size="page_size" />
    <DxPager :visible="true" :allowed-page-sizes="[5, 10, 'all']" :display-mode="'full'" :show-page-size-selector="true"
      :show-info="true" :show-navigation-buttons="true" />
    <DxFilterRow :visible="true" :apply-filter="'auto'" />
    <DxHeaderFilter :visible="true" />
    <DxSearchPanel :visible="true" :width="240" placeholder="Search..." />
    <DxColumnChooser :enabled="true" />
    <DxColumnFixing :enabled="true" />
    <DxColumn type="buttons" :width="110">
      <DxButton hint="Copy" icon="copy" @click="copyIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Details" icon="link" @click="detailsIconClick" />
      <DxButton :disabled="command_buttons_disabled" hint="Related Entry" icon="share" @click="relationIconClick" />
    </DxColumn>
    <DxColumn :width="60" :fixed="true" data-field="Target_ID" caption="ID" cell-template="id-cell-template">
    </DxColumn>
    <template #id-cell-template="{ data }">
      {{ data.data["Target_ID"] }}
    </template>
    <DxColumn :width="340" :fixed="true" data-field="Target_Name" caption="Name">
    </DxColumn>
    <DxColumn :width="80" data-field="Target_GENE" caption="Gene">
    </DxColumn>
    <DxColumn :width="80" data-field="Target_Action" caption="Action">
    </DxColumn>
    <DxColumn :width="200" data-field="Target_Class" caption="Class">
    </DxColumn>
    <DxColumn :width="120" data-field="UniProtKB_ID" caption="UniProtKB ID" cell-template="uniprotkb_id-cell-template">
    </DxColumn>
    <template #uniprotkb_id-cell-template="{ data }">
      <a :href="'https://www.uniprot.org/uniprot/' + data.data['UniProtKB_ID']" target="_blank">{{
          data.data["UniProtKB_ID"]
      }}</a>
    </template>
    <DxColumn :width="200" data-field="UniProtKB_Entry_Name" caption="UniProtKB Entry Name">
    </DxColumn>
    <DxColumn :width="120" data-field="ChEMBL_ID" caption="ChEMBL ID" cell-template="chembl_id-cell-template">
    </DxColumn>
    <template #chembl_id-cell-template="{ data }">
      <a :href="'https://www.ebi.ac.uk/chembl/target_report_card/' + data.data['ChEMBL_ID']" target="_blank">{{
          data.data["ChEMBL_ID"]
      }}</a>
    </template>
    <DxMasterDetail :enabled="true" template="masterDetailTemplate" />
    <template #masterDetailTemplate="{ data: target_detail }">
      <TargetDetailTemplate :template-data="target_detail" />
    </template>
  </DxDataGrid>
  <DxToast
    :max-width="400"
    :position="' center'"
    v-model:visible="isToastVisible"
    v-model:message="toastMessage"
    v-model:type="toastType"
  />
</template>

<script>
import { Streamlit } from 'streamlit-component-lib'
import { toRaw } from '@vue/reactivity'
import { toClipboard } from '@soerenmartius/vue3-clipboard'

import {
  DxDataGrid,
  DxColumn,
  DxColumnChooser,
  DxColumnFixing,
  DxHeaderFilter,
  DxSearchPanel,
  DxFilterRow,
  DxPaging,
  DxPager,
  DxMasterDetail,
  DxButton,
} from 'devextreme-vue/data-grid'
import { DxToast } from 'devextreme-vue/toast'

import TrialDetailTemplate from './components/TrialDetailTemplate.vue'
import DrugDetailTemplate from './components/DrugDetailTemplate.vue'
import TargetDetailTemplate from './components/TargetDetailTemplate.vue'

export default {
  components: {
    DxDataGrid,
    DxColumn,
    DxColumnChooser,
    DxColumnFixing,
    DxHeaderFilter,
    DxSearchPanel,
    DxFilterRow,
    DxPaging,
    DxPager,
    DxMasterDetail,
    DxButton,
    DxToast,
    TrialDetailTemplate,
    DrugDetailTemplate,
    TargetDetailTemplate,
  },
  data() {
    return {
      data_type: '',
      trials: [],
      drugs: [],
      targets: [],

      trialsTableRef: 'trialsTable',
      drugsTableRef: 'drugsTable',
      targetsTableRef: 'targetsTable',

      command_buttons_disabled: false,
      page_size: 10,

      isToastVisible: false,
      toastMessage: "",
      toastType: "info",
    };
  },
  methods: {
    onRender(event) {
      this.command_buttons_disabled = event.detail.args['command_buttons_disabled']
      const dt = event.detail.args['data']['type']
      this.data_type = dt
      if (dt == 'trials') {
        this.trials = event.detail.args['data']['data']
      } else if (dt == 'drugs') {
        this.drugs = event.detail.args['data']['data']
      } else if (dt == 'targets') {
        this.targets = event.detail.args['data']['data']
      }
      Streamlit.setFrameHeight()
    },
    copyIconClick(e) {
      toClipboard(JSON.stringify(toRaw(e.row.data)))
      this.toastType = 'success'
      this.toastMessage = 'Copied To Clipboard!'
      this.isToastVisible = true
    },
    detailsIconClick(e) {
      if (e.element.id == 'trials_table') {
        Streamlit.setComponentValue({
          data_type: 'trial',
          action: 'details',
          item_id: toRaw(e.row.data)['Trial_ID']
        })
      } else if (e.element.id == 'drugs_table') {
        Streamlit.setComponentValue({
          data_type: 'drug',
          action: 'details',
          item_id: toRaw(e.row.data)['Drug_ID']
        })
      } else if (e.element.id == 'targets_table') {
        Streamlit.setComponentValue({
          data_type: 'target',
          action: 'details',
          item_id: toRaw(e.row.data)['Target_ID']
        })
      }
      e.event.preventDefault();
    },
    relationIconClick(e) {
      if (e.element.id == 'trials_table') {
        Streamlit.setComponentValue({
          data_type: 'trial',
          action: 'relations',
          target: toRaw(e.row.data)['Trial_ID']
        })
      } else if (e.element.id == 'drugs_table') {
        Streamlit.setComponentValue({
          data_type: 'drug',
          action: 'relations',
          item_id: toRaw(e.row.data)['Drug_ID']
        })
      } else if (e.element.id == 'targets_table') {
        Streamlit.setComponentValue({
          data_type: 'target',
          action: 'relations',
          item_id: toRaw(e.row.data)['Target_ID']
        })
      }
      e.event.preventDefault();
    }
  },
  updated() {
    Streamlit.setFrameHeight()
  },
  mounted() {
    Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, this.onRender)
    Streamlit.setComponentReady()
    Streamlit.setFrameHeight()
  },
};
</script>
